// ==========================================
// MAIN COMPONENT
// ==========================================

function TicketsPageContent() {
  const searchParams = useSearchParams()
  const [tickets, setTickets] = useState<Ticket[]>(mockTickets)
  const [filteredTickets, setFilteredTickets] = useState<Ticket[]>(mockTickets)
  const [selectedTickets, setSelectedTickets] = useState<Set<string>>(new Set())
  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState<string>(searchParams.get('status') || 'all')
  const [priorityFilter, setPriorityFilter] = useState<string>(searchParams.get('priority') || 'all')
  const [categoryFilter, setCategoryFilter] = useState<string>('all')
  const [sortBy, setSortBy] = useState<'newest' | 'oldest' | 'priority' | 'updated'>('newest')
  const [viewMode, setViewMode] = useState<'list' | 'grid'>('list')
  
  // Apply filters
  useEffect(() => {
    let result = [...tickets]
    
    // Search
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      result = result.filter(t => 
        t.subject.toLowerCase().includes(query) ||
        t.description.toLowerCase().includes(query) ||
        t.customerName.toLowerCase().includes(query) ||
        t.customerEmail.toLowerCase().includes(query) ||
        t.number.toLowerCase().includes(query)
      )
    }
    
    // Status filter
    if (statusFilter !== 'all') {
      result = result.filter(t => t.status === statusFilter)
    }
    
    // Priority filter
    if (priorityFilter !== 'all') {
      result = result.filter(t => t.priority === priorityFilter)
    }
    
    // Category filter
    if (categoryFilter !== 'all') {
      result = result.filter(t => t.category === categoryFilter)
    }
    
    // Sort
    result.sort((a, b) => {
      switch (sortBy) {
        case 'newest':
          return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
        case 'oldest':
          return new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
        case 'updated':
          return new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime()
        case 'priority':
          const priorityOrder = { critical: 0, urgent: 1, high: 2, medium: 3, low: 4 }
          return (priorityOrder[a.priority] || 3) - (priorityOrder[b.priority] || 3)
        default:
          return 0
      }
    })
    
    setFilteredTickets(result)
  }, [tickets, searchQuery, statusFilter, priorityFilter, categoryFilter, sortBy])
  
  // Format time ago
  const formatTimeAgo = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now.getTime() - date.getTime()
    const diffMins = Math.floor(diffMs / 60000)
    const diffHours = Math.floor(diffMs / 3600000)
    const diffDays = Math.floor(diffMs / 86400000)
    
    if (diffMins < 60) return `${diffMins}m ago`
    if (diffHours < 24) return `${diffHours}h ago`
    return `${diffDays}d ago`
  }
  
  // Color helpers
  const getPriorityColor = (priority: string) => {
    const colors: Record<string, string> = {
      low: 'bg-gray-100 text-gray-600',
      medium: 'bg-blue-100 text-blue-600',
      high: 'bg-orange-100 text-orange-600',
      urgent: 'bg-red-100 text-red-600',
      critical: 'bg-red-200 text-red-700'
    }
    return colors[priority] || colors.medium
  }
  
  const getStatusColor = (status: string) => {
    const colors: Record<string, string> = {
      new: 'bg-purple-100 text-purple-600',
      open: 'bg-blue-100 text-blue-600',
      pending_customer: 'bg-yellow-100 text-yellow-600',
      pending_internal: 'bg-orange-100 text-orange-600',
      on_hold: 'bg-gray-100 text-gray-600',
      resolved: 'bg-green-100 text-green-600',
      closed: 'bg-gray-200 text-gray-500'
    }
    return colors[status] || colors.open
  }
  
  const getCategoryIcon = (category: string) => {
    const icons: Record<string, string> = {
      general: 'fa-question-circle',
      billing: 'fa-credit-card',
      technical: 'fa-wrench',
      bug_report: 'fa-bug',
      feature_request: 'fa-lightbulb',
      account: 'fa-user',
      integration: 'fa-plug',
      onboarding: 'fa-rocket',
      cancellation: 'fa-times-circle',
      other: 'fa-ellipsis-h'
    }
    return icons[category] || icons.general
  }
  
  const getSourceIcon = (source: string) => {
    const icons: Record<string, string> = {
      email: 'fa-envelope',
      chat: 'fa-comment',
      web_form: 'fa-globe',
      phone: 'fa-phone',
      api: 'fa-code',
      social: 'fa-share-alt',
      internal: 'fa-building'
    }
    return icons[source] || icons.email
  }
  
  // Toggle ticket selection
  const toggleTicketSelection = (ticketId: string) => {
    const newSelection = new Set(selectedTickets)
    if (newSelection.has(ticketId)) {
      newSelection.delete(ticketId)
    } else {
      newSelection.add(ticketId)
    }
    setSelectedTickets(newSelection)
  }
  
  // Select all tickets
  const toggleSelectAll = () => {
    if (selectedTickets.size === filteredTickets.length) {
      setSelectedTickets(new Set())
    } else {
      setSelectedTickets(new Set(filteredTickets.map(t => t.id)))
    }
  }
  
  // Status counts
  const statusCounts = {
    all: tickets.length,
    new: tickets.filter(t => t.status === 'new').length,
    open: tickets.filter(t => t.status === 'open').length,
    pending_customer: tickets.filter(t => t.status === 'pending_customer').length,
    resolved: tickets.filter(t => t.status === 'resolved').length
  }

  return (
    <div className="space-y-6">
      {/* Filters Bar */}
      <div className="bg-white rounded-xl border border-gray-200 p-4">
        <div className="flex flex-col lg:flex-row lg:items-center gap-4">
          {/* Search */}
          <div className="flex-1 relative">
            <i className="fa-solid fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
            <input
              type="text"
              placeholder="Search tickets by subject, customer, or ticket number..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            />
          </div>
          
          {/* Filters */}
          <div className="flex flex-wrap items-center gap-3">
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent text-sm"
            >
              <option value="all">All Status ({statusCounts.all})</option>
              <option value="new">New ({statusCounts.new})</option>
              <option value="open">Open ({statusCounts.open})</option>
              <option value="pending_customer">Pending Customer ({statusCounts.pending_customer})</option>
              <option value="resolved">Resolved ({statusCounts.resolved})</option>
            </select>
            
            <select
              value={priorityFilter}
              onChange={(e) => setPriorityFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent text-sm"
            >
              <option value="all">All Priority</option>
              <option value="critical">Critical</option>
              <option value="urgent">Urgent</option>
              <option value="high">High</option>
              <option value="medium">Medium</option>
              <option value="low">Low</option>
            </select>
            
            <select
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent text-sm"
            >
              <option value="all">All Categories</option>
              <option value="general">General</option>
              <option value="billing">Billing</option>
              <option value="technical">Technical</option>
              <option value="bug_report">Bug Report</option>
              <option value="feature_request">Feature Request</option>
              <option value="integration">Integration</option>
            </select>
            
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as any)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent text-sm"
            >
              <option value="newest">Newest First</option>
              <option value="oldest">Oldest First</option>
              <option value="updated">Recently Updated</option>
              <option value="priority">Priority</option>
            </select>
            
            {/* View Toggle */}
            <div className="flex items-center border border-gray-300 rounded-lg overflow-hidden">
              <button
                onClick={() => setViewMode('list')}
                className={`px-3 py-2 ${viewMode === 'list' ? 'bg-purple-100 text-purple-600' : 'text-gray-600 hover:bg-gray-50'}`}
              >
                <i className="fa-solid fa-list"></i>
              </button>
              <button
                onClick={() => setViewMode('grid')}
                className={`px-3 py-2 ${viewMode === 'grid' ? 'bg-purple-100 text-purple-600' : 'text-gray-600 hover:bg-gray-50'}`}
              >
                <i className="fa-solid fa-th-large"></i>
              </button>
            </div>
          </div>
        </div>
        
        {/* Bulk Actions */}
        {selectedTickets.size > 0 && (
          <div className="mt-4 pt-4 border-t border-gray-200 flex items-center gap-4">
            <span className="text-sm text-gray-600">
              {selectedTickets.size} ticket{selectedTickets.size > 1 ? 's' : ''} selected
            </span>
            <button className="px-3 py-1.5 text-sm bg-blue-100 text-blue-600 rounded-lg hover:bg-blue-200 transition-colors">
              <i className="fa-solid fa-user-plus mr-1"></i> Assign
            </button>
            <button className="px-3 py-1.5 text-sm bg-green-100 text-green-600 rounded-lg hover:bg-green-200 transition-colors">
              <i className="fa-solid fa-check mr-1"></i> Resolve
            </button>
            <button className="px-3 py-1.5 text-sm bg-gray-100 text-gray-600 rounded-lg hover:bg-gray-200 transition-colors">
              <i className="fa-solid fa-tag mr-1"></i> Add Tag
            </button>
            <button className="px-3 py-1.5 text-sm bg-red-100 text-red-600 rounded-lg hover:bg-red-200 transition-colors">
              <i className="fa-solid fa-trash mr-1"></i> Delete
            </button>
          </div>
        )}
      </div>
      
      {/* Tickets List */}
      <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
        {/* List Header */}
        <div className="px-5 py-3 bg-gray-50 border-b border-gray-200 flex items-center gap-4">
          <input
            type="checkbox"
            checked={selectedTickets.size === filteredTickets.length && filteredTickets.length > 0}
            onChange={toggleSelectAll}
            className="w-4 h-4 rounded border-gray-300 text-purple-600 focus:ring-purple-500"
          />
          <span className="text-sm text-gray-600">
            Showing {filteredTickets.length} of {tickets.length} tickets
          </span>
        </div>
        
        {/* Tickets */}
        {viewMode === 'list' ? (
          <div className="divide-y divide-gray-100">
            {filteredTickets.map(ticket => (
              <div 
                key={ticket.id}
                className={`px-5 py-4 hover:bg-gray-50 transition-colors ${selectedTickets.has(ticket.id) ? 'bg-purple-50' : ''}`}
              >
                <div className="flex items-start gap-4">
                  <input
                    type="checkbox"
                    checked={selectedTickets.has(ticket.id)}
                    onChange={() => toggleTicketSelection(ticket.id)}
                    className="mt-1 w-4 h-4 rounded border-gray-300 text-purple-600 focus:ring-purple-500"
                  />
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2 mb-1 flex-wrap">
                      <Link 
                        href={`/sub-account/support-hub/tickets/${ticket.id}`}
                        className="text-xs text-gray-500 font-mono hover:text-purple-600"
                      >
                        {ticket.number}
                      </Link>
                      <span className={`px-2 py-0.5 rounded-full text-xs font-medium ${getPriorityColor(ticket.priority)}`}>
                        {ticket.priority}
                      </span>
                      <span className={`px-2 py-0.5 rounded-full text-xs font-medium ${getStatusColor(ticket.status)}`}>
                        {ticket.status.replace(/_/g, ' ')}
                      </span>
                      <span className="px-2 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-600 flex items-center gap-1">
                        <i className={`fa-solid ${getCategoryIcon(ticket.category)} text-[10px]`}></i>
                        {ticket.category.replace(/_/g, ' ')}
                      </span>
                    </div>
                    
                    <Link 
                      href={`/sub-account/support-hub/tickets/${ticket.id}`}
                      className="font-medium text-gray-900 hover:text-purple-600 line-clamp-1"
                    >
                      {ticket.subject}
                    </Link>
                    
                    <p className="text-sm text-gray-500 mt-1 line-clamp-1">
                      {ticket.description}
                    </p>
                    
                    <div className="flex items-center gap-4 mt-2 text-xs text-gray-500">
                      <span className="flex items-center gap-1">
                        <i className="fa-solid fa-user"></i>
                        {ticket.customerName}
                      </span>
                      <span className="flex items-center gap-1">
                        <i className={`fa-solid ${getSourceIcon(ticket.source)}`}></i>
                        {ticket.source}
                      </span>
                      {ticket.assignedName && (
                        <span className="flex items-center gap-1">
                          <i className="fa-solid fa-user-check"></i>
                          {ticket.assignedName}
                        </span>
                      )}
                      {ticket.tags.length > 0 && (
                        <span className="flex items-center gap-1">
                          <i className="fa-solid fa-tags"></i>
                          {ticket.tags.slice(0, 2).join(', ')}
                          {ticket.tags.length > 2 && ` +${ticket.tags.length - 2}`}
                        </span>
                      )}
                    </div>
                  </div>
                  
                  <div className="text-right flex-shrink-0">
                    <p className="text-xs text-gray-500">{formatTimeAgo(ticket.createdAt)}</p>
                    {ticket.aiConfidence && (
                      <div className="mt-2 flex items-center gap-1 justify-end">
                        <i className="fa-solid fa-robot text-purple-400 text-xs"></i>
                        <span className="text-xs text-purple-600">{Math.round(ticket.aiConfidence * 100)}%</span>
                      </div>
                    )}
                    {ticket.aiSuggestedResponse && (
                      <div className="mt-1">
                        <span className="text-xs px-2 py-0.5 bg-purple-100 text-purple-600 rounded-full">
                          AI Response Ready
                        </span>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="p-4 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {filteredTickets.map(ticket => (
              <Link
                key={ticket.id}
                href={`/sub-account/support-hub/tickets/${ticket.id}`}
                className={`block p-4 border rounded-xl hover:border-purple-300 hover:shadow-md transition-all ${
                  selectedTickets.has(ticket.id) ? 'border-purple-500 bg-purple-50' : 'border-gray-200'
                }`}
              >
                <div className="flex items-start justify-between mb-2">
                  <span className="text-xs text-gray-500 font-mono">{ticket.number}</span>
                  <span className={`px-2 py-0.5 rounded-full text-xs font-medium ${getPriorityColor(ticket.priority)}`}>
                    {ticket.priority}
                  </span>
                </div>
                
                <h3 className="font-medium text-gray-900 line-clamp-2 mb-2">{ticket.subject}</h3>
                
                <div className="flex items-center gap-2 mb-3">
                  <span className={`px-2 py-0.5 rounded-full text-xs font-medium ${getStatusColor(ticket.status)}`}>
                    {ticket.status.replace(/_/g, ' ')}
                  </span>
                  <span className="text-xs text-gray-500">{ticket.customerName}</span>
                </div>
                
                <div className="flex items-center justify-between text-xs text-gray-500">
                  <span>{formatTimeAgo(ticket.createdAt)}</span>
                  {ticket.aiConfidence && (
                    <span className="flex items-center gap-1 text-purple-600">
                      <i className="fa-solid fa-robot"></i>
                      {Math.round(ticket.aiConfidence * 100)}%
                    </span>
                  )}
                </div>
              </Link>
            ))}
          </div>
        )}
        
        {/* Empty State */}
        {filteredTickets.length === 0 && (
          <div className="px-5 py-12 text-center">
            <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <i className="fa-solid fa-ticket text-gray-400 text-2xl"></i>
            </div>
            <h3 className="font-medium text-gray-900 mb-1">No tickets found</h3>
            <p className="text-sm text-gray-500">
              {searchQuery || statusFilter !== 'all' || priorityFilter !== 'all' || categoryFilter !== 'all'
                ? 'Try adjusting your filters'
                : 'Create your first support ticket to get started'
              }
            </p>
          </div>
        )}
      </div>
    </div>
  )
}

export default function TicketsPage() {
  return (
    <Suspense fallback={<div className="min-h-screen bg-gray-50 dark:bg-[#0a0a0a] flex items-center justify-center"><div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div></div>}>
      <TicketsPageContent />
    </Suspense>
  )
}
